import React, { Component } from 'react'
import { Carousel, WingBlank, Grid, Tabs, WhiteSpace } from 'antd-mobile'
import { StickyContainer, Sticky } from 'react-sticky'
import {withRouter} from "react-router-dom"
import ServiceBar from '../components/serviceBar'
import Case from '../components/case'
import NewsList from '../components/newsList'
import './home.css'
import banner from '../images/banner.png'
import place1 from '../images/place1.png'
import p1 from '../images/p1.png'
import p2 from '../images/p2.png'
import p3 from '../images/p3.png'
import p4 from '../images/p4.png'
import p5 from '../images/p5.png'
import p6 from '../images/p6.png'
import p7 from '../images/p7.png'
import p8 from '../images/p8.png'
import p9 from '../images/p9.png'
import caseImg from '../images/case.png'
import caseImg2 from '../images/case2.png'

class Home extends Component {
    constructor(props) {
        super(props)
        this.state = {
            data: ['1', '2', '3'],
            tabs: [
                { title: '' },
                { title: '' }
            ],
            imgHeight: 176,
            placeData: [
                {
                    icon: p1,
                    text: ''
                },
                {
                    icon: p2,
                    text: ''
                },
                {
                    icon: p3,
                    text: ''
                },
                {
                    icon: p4,
                    text: ''
                },
                {
                    icon: p5,
                    text: ''
                },
                {
                    icon: p6,
                    text: ''
                },
                {
                    icon: p7,
                    text: ''
                },
                {
                    icon: p8,
                    text: ''
                },
                {
                    icon: p9,
                    text: ''
                }
            ],
            caseData: [
                {
                    img: caseImg,
                    title: '',
                    desc: ''
                },
                {
                    img: caseImg2,
                    title: '',
                    desc: ''
                },
                {
                    img: caseImg2,
                    title: '',
                    desc: ''
                },
                {
                    img: caseImg,
                    title: '',
                    desc: ''
                },
                {
                    img: caseImg2,
                    title: '',
                    desc: ''
                },
                {
                    img: caseImg2,
                    title: '',
                    desc: ''
                }
            ],
            newsData: [
                {
                    desc: ' ',
                    date:'2018-05-13'
                },
                {
                    desc: ' ',
                    date:'2018-05-23'
                },
                {
                    desc: ' ',
                    date:'2018-11-10'
                }
            ],
            IndustryData: [
                {
                    desc: '',
                    date:'2017-06-13'
                },
                {
                    desc: ' ',
                    date:'2018-07-24'
                },
                {
                    desc: ' ',
                    date:'2018-11-10'
                }
            ]
        }
    }

    renderTabBar(props) {
        return (<Sticky>
            {({ style }) => <div style={{ ...style, zIndex: 1 }}><Tabs.DefaultTabBar {...props} /></div>}
        </Sticky>);
    }

    render() {
        return (
            <div>
                <WingBlank>
                    <Carousel
                    autoplay={false}
                    infinite
                    >
                    {this.state.data.map(val => (
                        <a
                        key={val}
                        href="javascript:void(0)"
                        style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
                        >
                        <img
                            src={banner}
                            alt=""
                            style={{ width: '100%', verticalAlign: 'top' }}
                            onLoad={() => {
                            // fire window resize event to change height
                            window.dispatchEvent(new Event('resize'));
                            this.setState({ imgHeight: 'auto' });
                            }}
                        />
                        </a>
                    ))}
                    </Carousel>
                </WingBlank>
                <h2 className="home-sub-title"> </h2>
                <ServiceBar />
                <h2 className="home-sub-title"></h2>
                <WingBlank>
                    <Carousel
                    autoplay={false}
                    infinite
                    >
                    {this.state.data.map(val => (
                        <a
                        key={val}
                        href="javascript:void(0)"
                        style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
                        >
                        <img
                            src={place1}
                            alt=""
                            style={{ width: '100%', verticalAlign: 'top' }}
                            onLoad={() => {
                            // fire window resize event to change height
                            window.dispatchEvent(new Event('resize'));
                            this.setState({ imgHeight: 'auto' });
                            }}
                        />
                        </a>
                    ))}
                    </Carousel>
                </WingBlank>
                <div className="palce-grid-layout">
                    <Grid data={this.state.placeData} activeStyle={false} columnNum={3} />
                </div>

                <h2 className="home-sub-title"></h2>
                <div className="home-content">
                    {
                        this.state.caseData.map((item,index) => {
                            return <Case data={item} index={index} key={index} />
                        })
                    }
                </div>

                <div className="public-container">
                    <WhiteSpace />
                        <StickyContainer>
                        <Tabs tabs={this.state.tabs}
                            initalPage={'t2'}
                            renderTabBar={this.renderTabBar}
                        >
                            <div style={{ display: 'flex', alignItems: 'center',backgroundColor: '#fff' }}>
                                <NewsList data={this.state.newsData} />
                            </div>
                            <div style={{ display: 'flex', alignItems: 'center', backgroundColor: '#fff' }}>
                                <NewsList data={this.state.IndustryData} />
                            </div>
                        </Tabs>
                        </StickyContainer>
                    <WhiteSpace />
                    <a href="javascript:void(0)" className="more-btn"></a>
                </div>

            </div>
        )
    }
}

export default withRouter(Home)


