import React, { Component } from 'react'
import './appFooter.css'
import f1 from '../images/f1.png'
import f2 from '../images/f2.png'
import f3 from '../images/f3.png'
import f4 from '../images/f4.png'
import tel from '../images/tel.png'
import wechat from '../images/wechat.png'
import qrcode from '../images/qrcode.png'

class appFooter extends Component {
    constructor(props) {
        super(props)
    }

    render() {
        return (
            <div>
                <div className="public-container">
                    <div className="f-item-layout">
                        <div className="f-img-layout">
                            <img src={f1} alt="" />
                        </div>
                        <div className="f-content-layout">
                            <span></span>
                            <p></p>
                        </div>
                    </div>
                    <div className="f-item-layout">
                        <div className="f-img-layout">
                            <img src={f2} alt="" />
                        </div>
                        <div className="f-content-layout">
                            <span></span>
                            <p></p>
                        </div>
                    </div>
                    <div className="f-item-layout">
                        <div className="f-img-layout">
                            <img src={f3} alt="" />
                        </div>
                        <div className="f-content-layout">
                            <span></span>
                            <p>100%</p>
                        </div>
                    </div>
                    <div className="f-item-layout">
                        <div className="f-img-layout">
                            <img src={f4} alt="" />
                        </div>
                        <div className="f-content-layout">
                            <span></span>
                            <p>PCO</p>
                        </div>
                    </div>
                    <div className="clearfix"></div>
                    <a type="phone" href="tel:18874977350" className="contact-layout-btn"><img src={tel} alt="" /></a>
                </div>

                <div className="footer-layout">
                    <div className="footer-left">
                        <img src={wechat} className="wechat-laout" alt="" />
                        <div className="footer-text">
                            <br/>
                            <br/>
                            
                        </div>
                    </div>
                    <img src={qrcode} className="qrcode-layout" alt="" />
                </div>
            </div>
        )
    }
}

export default appFooter


